body{
    font-family: "Open Sans", sans-serif;
    color: #272829;
    margin: 0;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    text-align: left;
    background-color: #fff;
}

*, ::after, ::before{
    box-sizing: border-box;
}

.nav-toggle {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: right;
    min-width: 95%;
}

.hidden-nav-bar{
    transform: translateX(-100%); 
    }     


/*hamburger style*/
@media (min-width: 1020px) {
    .nav-toggle {
      display: none; 
    }
  }
